.Img {
    width: 90px;
    margin-right: 5px;
    margin-bottom: 2px;
    display: inline-table;
    text-align: center;
    font-size: small;
}

.Break {
    flex-basis: 100%;
    height: 0;
}

.Img img {
    border: 1px solid;
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, 0.1);
}

.Gmax {
    background-image: url(https://raw.githubusercontent.com/Leanny/leanny.github.io/master/seedchecker/img/etc/gmax.png);
    background-repeat: no-repeat;
    background-position-x: 32px;
}

.NoShiny {
    background-image: url(https://raw.githubusercontent.com/Leanny/leanny.github.io/master/seedchecker/img/etc/no_shiny.png);
    background-repeat: no-repeat;
    background-position-x: 64px;
    background-position-y: 5px;
}

.Shiny {
    background-image: url(https://raw.githubusercontent.com/Leanny/leanny.github.io/master/seedchecker/img/etc/shiny.png);
    background-repeat: no-repeat;
    background-position-x: 64px;
    background-position-y: 5px;
}