.Wrapper {
    background-color: white;
    border: 1px solid #dee2e6;
    padding: 15px;
    border-radius: 1rem;
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
    text-decoration: none;
    color: black;
}

.Break {
    flex-basis: 100%;
    height: 0;
}

.GameWrapper {
    margin-top: 20px;
    padding: 5px;
    border-radius: 0.75rem;
    width: 48%;
    margin-right: 1%;
    border: 1px solid rgba(0.5, 0.5, 0.5, 0.5);
    background-color: rgba(0.5, 0.5, 0.5, 0.1);    
}