.Error {
    background-color: white;
    text-align: center;
    position: relative;
    display: inline-block;
    margin-top: 10%;
    vertical-align: center;
    width: 100%;
    border: 1px solid black;
    padding: 100px;
    font-weight: bold;
    color: red;
    font-size: xx-large;
}

.Wrapper {
    background-color: white;
    border: 1px solid #dee2e6;
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    text-decoration: none;
    color: black;
    font-size: small;
}

.MonWrapper {
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.Entry {
    border: 1px solid black;
    border-radius: 0.75rem;
    margin-right: 5px;
    margin-bottom: 5px;
    padding-top: 5px;
    padding-right: 10px;
    padding-left: 10px;
    padding-bottom: 5px;
    line-height: 1rem;
}

.Entry ul {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
    width: 100%;
}

.Entry li {
    padding-left: 5px;
}

.Break {
    flex-basis: 100%;
    height: 0;
}

.Center {
    text-align: center;
}

.Item {
    padding: 0px;
    margin: 0px;
}