.ContentTable {
    margin-left: 30px;
    padding-left: 10px;
}

.Table {
    border: 1px solid #212529;
    border-collapse: collapse;
    width: 100%;
    color: #212529;
    text-align: center;
}

.Table table, th, td {
    border: 1px solid #212529;
    padding-left: 5px;
    padding-right: 5px;
    min-width: 50px;
}

.TableHead {
    padding: .75rem;
    background-color: rgb(148, 148, 148);
    border: 1px solid #212529;
}

.Icon {
    padding-top: 10px;
    padding-left: 0;
    padding-right: 0;
}

.Sword {
    background-color: royalblue;
}

.Shield {
    background-color: orangered;
}

.Both {
    background-color: white;
}

.MonWrapper {
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
}

.Entry {
    border: 1px solid black;
    border-radius: 0.75rem;
    margin-right: 5px;
    margin-bottom: 5px;
    padding-top: 5px;
    padding-right: 10px;
    padding-left: 10px;
    line-height: 1rem;
    font-size: small;
}

.Entry ul {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
    width: 100%;
}

.Entry li {
    padding-left: 5px;
}


.Center {
    text-align: center;
}

@media screen and (min-width: 770px) {
    .Container {
        display: flex;
    }

    .Break {
        flex-basis: 100%;
        height: 0;
    }
}

@media screen and (min-width: 1040px) {
    .ContentTable {
        margin-top: -63px;
    }

    .DetailMapMobile {
        margin-left: 0px;
    }

    .TableScroll {
        height: 390px;
        padding-right: 10px;
        overflow: scroll;
    }

    .DetailedMapCommon {
        height: 390px;
        min-width: 520px;
        width: 520px;
        min-height: 390px;
    }
}
